const FilterCell=({title,list,addDataToFilters,sideFilter})=>{

  const [isModalShow,toggleModel]=useState(0);
  const [searchShow,setSearchShow]=useState(0);

  const onChangeFilter = e =>{
    const {value,checked}=e.target;
    addDataToFilters(value);

  }
  const copy=[...list];

  console.log();
  

  
  

  return (
    <div className="filter_box">
    <div className="filter-search">
      <h3 className="f-15">
        {title}
        {/* {
          list.length>=5 
          ?
        } */}
        {/* <i className="sprite-img small-search-icon float-right" onClick={()=>setSearchShow(1)}></i> */}
      </h3>
      {
        searchShow
        ?
        <div className="fiter-search-box">
          <input
            autoFocus
            type="text"
            className="f-13"
            placeholder="Search for Categories"
          />
          <i className="cross_icon" onClick={()=>setSearchShow(0)}></i>
        </div>
      : null
      }
     
    </div>
    
    {
      list.map((d,i)=>(
        i<=5 ?
        <ul className="list-block f-14" key={i}>
          <li>
            <div className="checkbox_field">
              <input type="checkbox" checked={sideFilter[d.id]} onChange={onChangeFilter} value={d.id} id={d.id} />
              <label for={d.id}>
                <span>{d.name}</span>
              </label>
            </div>
          </li>
        </ul>
        : null
      ))
    }
  
  {
    list.length>=5 
    ?
    <p
      className="mT15 text-red f-14"
      onClick={() => toggleModel(1)}
    >
      +{list.length-5} More
    </p>
    : null
  }
    
    {
    isModalShow === 1 ? (
      <div className="modal">
        <div className="modal-body">
          <div className="modal-container">
            <AuthorSearchModal sideFilter={sideFilter} list={copy.splice(5,list.length)} addDataToFilters={addDataToFilters} name={title}/>
            <i
              className="sprite-img cross-icon"
              onClick={() => toggleModel(0)}
            ></i>
          </div>
        </div>
        <div
          className="menu-mobile__bg"
          onClick={() => toggleModel(0)}
        ></div>
      </div>
    ) : null
    }
  </div>
  )
}





