const AuthorSearchModal = ({list,name,addDataToFilters,sideFilter}) => {

  const [inputSearch,setInput]=useState('');

  const filtersAtoAlpha = list =>{
    return alphaArr.map(a=>{
        return {
          key:a,
          data:list.filter(d=> d.name[0].toLowerCase() === a.toLowerCase())
        }
    })
  }


  const filterData=list.filter(d=>d.name.toLowerCase().includes(inputSearch.toLowerCase()))

  return(
  <div class="author-search">
    <div className="author-search-head row">
      <div className="col-xl-4 col-lg-4">
        <input type="search" placeholder={"Search "+name} onChange={(e)=>setInput(e.target.value)} />
      </div>
      <div className="col-xl-8 col-lg-8">
        <ul className="list-inline F-12">
          {
            alphaArr.map((d,i)=>(
              <li key={i}>
                <a href={"#"+d}>{d}</a>
              </li>
            ))
          }
        </ul>
      </div>
    </div>
    <div className="author-search-body">
      {
        filtersAtoAlpha(filterData).map((data,index)=>(
          data.data.length ?
          <div className="author_flex_box" key={index}>
            <h3 className="f-15" id={data.key}>
              {data.key}
            </h3>
            <ul className="list-block f-14">
              {
                data.data.map((d,i)=>(
                  <li key={i}>
                    <div className="checkbox_field">
                      <input type="checkbox" class="" checked={sideFilter[d.id]} value={d.id} onChange={e=>addDataToFilters(e.target.value)} id={d.id} />
                      <label for={d.id}>
                        <span>{d.name}</span>
                      </label>
                    </div>
                  </li>
                ))
              }
            </ul>
          </div>
          : null
        ))
      }  
    </div>
  </div>
)
};



