import React, { Component,useState } from 'react';
import { Book } from "../common/Icons";
import ProfileAddress from "../components/ProfileAddress";
import PriceDetails from "../components/PriceDetails";
import MainLayout from '../layouts/MainLayout';
import Link from '../common/Link';
import {email,fullname,phone} from '../common/Auth';


const Tabs=[
  {
    name:"Profile",
    id:"editprofile"
  },
  {
    name:"Orders",
    id:"orders"
  },
  {
    name:"Addresses",
    id:"addresses"
  },
  {
    name:"Contact Us",
    id:"contactus"
  },
  
]

class EditProfile extends Component {

  state={
    tab:"editprofile"
  }

  componentDidMount(){
    const {type}=this.props.match.params;
    if(Tabs.filter(d=>d.id===type).length){
      this.setState({tab:type})
    }
  }

  static getDerivedStateFromProps(props, state) {
    if (props.match.params.type !== state.tab) {
      return {
        tab: props.match.params.type
      };
    }
    return null;
  }

  changeTab=id=>this.setState({tab:id})

  render() {
    const {tab}=this.state;
    return (
      <MainLayout>
         <section className="cart section-spacing">
          <div className="container">
            <div className="profile">
              <ul className="list-inline profile-tab">
                {
                  Tabs.map((d,i)=>(
                    <li
                      key={i}
                      className={d.id===tab && "active"}
                    >
                      <Link to={`/user/${d.id}`} style={{display:'block'}} onClick={() => this.changeTab(d.id)}>
                        {d.name}
                      </Link>
                    </li>
                  ))
                }
              </ul>

              <div className="profile-tab-content">
                <div className="row">
                  <div className="col-lg-12 col-xl-12 ">
                    {
                    tab === Tabs[0].id 
                    ? 
                      <EditPro/>
                    : 
                    tab === Tabs[1].id 
                    ? 
                    <Orders/>
                    : 
                    tab === Tabs[2].id 
                    ? 
                    <ProfileAddress />
                     : 
                     tab === Tabs[3].id 
                     ? 
                      <p>Payment</p>
                     : 
                     null
                     }
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </MainLayout>
    );
  }
}

export default EditProfile;



